<template>
    <div class="hch">
        <div class="hch-top">
            <p class="hch-toptitle"> 精选商品</p>
         </div>
        <div class="hch-list" >
            <GoodComponent v-for="(good,i) of list" :key="i" class="hch-list-item" :good="good">
                <p slot="title" class="goodtitle">{{good.title}}</p>
            </GoodComponent>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        list:null
    }
}
</script>
<style lang="less">
    .hch{
        &-top{
            background: url("/static/img/xiu.png") no-repeat;
            background-position: center center;
            background-size:60%; 
            padding: 20px 0;
            line-height: 80px;
            text-align: center;
            font-size: 32px;/*px*/
        }
        &-list{
            display: flex;
            flex-wrap: wrap;
            // justify-content: space-around;
        }
    }
</style>



