<template>
    <div class="am">
        <ScrollView class="am-sv">
            <div class="content">
                <div class="user">
                    <div class="tou"></div>
                    <div class="right">
                        <p class="username">{{username}}</p>
                        <p class="dress">我的收货地址   <span class="jiantou">></span> </p>
                    </div>
                </div>
                <ul class="list">
                    <li class="item" v-for="(v,i) of list" :key="'user'+i">
                        <img :src="'static/img/图标/'+v.url" >
                        <p class="title">{{v.title}} <span class="jiantou">></span>     </p>
                    </li>
                </ul>
                <div class="btn" @click="logout()">
                    <span>退出登录</span>
                </div>
            </div>
            <FootBar></FootBar>
        </ScrollView>
    </div>
</template>
<script>
import FootBar from '../FootBar'
export default {
    data(){
        return {
            list:[{url:"upload_ie4wenzume4tsmjqmezdambqgqyde_56x56.png",title:"我的订单"},
            {url:"159_0ihj4c5a23kabh84ka8dfhl552dla_56x56.png",title:"我的购物车"},
            {url:"170419_7c5a4j17k0061bg4kiagf9lc69fe8_56x56.png",title:"我的拼团"},
            {url:"170803_840decficafea4lfg10gjc2a020gc_56x56.png",title:"我的钱包"},
            {url:"159_00ijba9e46e13970ec7kakll8a06a_56x56.png",title:"我的优惠券"},
            {url:"upload_ifqtsndeg43gkmrqmezdambqgyyde_56x56.png",title:"我收藏的商品"},
            {url:"upload_ie4wmy3gmq3wkmrqmezdambqgqyde_56x56.png",title:"我收藏的店铺"},
            {url:"upload_ie4tgm3ege4wkmrqmezdambqgqyde_56x56.png",title:"消息通知"},
            {url:"idid_ifrtqmdfgztggnzsg4zdambqhayde_84x84.png",title:"客服"},
            {url:"upload_ifrdonbugezgkmrqmezdambqmeyde_56x56.png",title:"意见反馈"},
            ],
            username:""
        }
    },
    components:{
        FootBar
    },
    methods:{
        logout(){
            sessionStorage.username="";
            this.$router.push("/login");
        }
    },
    mounted() {
        this.username=sessionStorage.username;
    },
}
</script>
<style lang="less">
    .am{
        height: 100%;
        padding-bottom: 100px;
        .am-sv{
            height: 100%;
            .content{
                padding-bottom: 50px;
                .jiantou{
                    margin-left: 10px;
                    float: right;
                    margin-right: 30px;
                    color: #999;
                }
                .user{
                    display: flex;
                    align-items: center;
                    height: 200px;
                    background-color: #fff;
                    .tou{
                        background: url("/static/img/tou.jpeg") no-repeat;
                        background-size: cover;
                        background-position: -15px;/*px*/
                        width: 120px;
                        height: 120px;
                        border-radius: 200px;
                        margin: 0 40px;
                    }
                    .right{
                        .username{
                            font-size: 40px;/*px*/
                            line-height: 60px;/*px*/
                            
                        }
                        .dress{
                            width: 100%;
                            font-size: 25px;/*px*/
                            color: #666;
                            
                        }
                    }
                  
                }
                .list{
                    .item{
                        display: flex;
                        align-items: center;
                        background-color: #fff;
                        padding-left: 20px;
                        height: 100px;/*px*/
                        line-height: 100px;/*px*/
                        font-size: 30px;/*px*/
                        img{
                            width: 70px;
                            height: 70px;
                            vertical-align: middle;
                            flex-basis: auto;
                            margin-right: 30px;
                        }
                        .title{
                            width:90%;
                            height: 100%;
                            border-bottom: 1px solid #ccc;/*px*/
        
                        }
                        &:first-child{
                            margin: 20px 0;
                            .title{
                                border-bottom: 0px solid white;/*px*/
                            }
                        }
                        &:nth-last-child(4){
                            margin-bottom: 20px;
                            .title{
                                border-bottom: 0px solid white;/*px*/
                            }
                        }
                         &:last-child{
                            margin-bottom:20px;
                            .title{
                                border-bottom: 0px solid white;/*px*/
                            }
                        }

                    }
                }
                .btn{
                    width: 80%;
                    margin: 30px auto;
                    height: 90px;
                    text-align: center;
                    line-height: 90px;
                    color: #fff;
                    background-color: #ff5777;
                    border-radius: 10px;
                    font-size: 30px;/*px*/
                }
            }
        }
    }
</style>



