import Api from '../../server/Api'
const state={
    SwiperList:[],
    TypeList:[],
    RecommendList:[],
    HotList:[],
    ChoosenList:[],
};
const mutations={
    setSwiperList(state,data){
        state.SwiperList=data
    },
    setTypeList(state,data){
        state.TypeList=data
    },
    setRecommendList(state,data){
        state.RecommendList=data
    },
    setHotList(state,data){
        state.HotList=data
    },
    setChoosenList(state,data){
        state.ChoosenList=data
    },
} ;
const actions={
    loadHomeData({commit}){
        let pidsParams={
            appPlat:"m",
            pids:'51822,106930,51833,51836'
        };
        let likeParams={
            pid:"9750",
            sort:"pop",
            cKey:"46"
        }
        Api.loadDataByPids(pidsParams)
            .then(({data})=>{
                commit("setSwiperList",data[51822].list);
                commit("setTypeList",data[106930].list);
                commit("setRecommendList",data[51833].list);
                commit("setHotList",data[51836].list);
            });
        Api.loadLikeData(likeParams)
            .then(({result})=>{
                commit("setChoosenList",result.wall.docs);
            })
    },
    
}
export default {
    state,
    mutations,
    actions
}


