<template>
    <div class="tc">
        <div class="tc-content">
            <TypeLeft :list="type.LeftList"></TypeLeft>
            <TypeRight :type="type.RightTypeList"></TypeRight>
        </div>
    </div>
</template>
<script>
import TypeLeft from './TypeLeft';
import TypeRight from './TypeRight';
import { mapState,mapMutations,mapActions } from "vuex";


export default {
    computed:{
        ...mapState(["type"])
    },
    components:{
        TypeLeft,TypeRight
    },
    methods:{
        ...mapActions(["loadLeftData","loadRightType","loadRightGood"])
    },
    mounted() {
        // 加载分类左侧和右侧数据
        this.loadLeftData();
        this.loadRightType();
    },
}
</script>
<style lang="less">
.tc{
    height: 100%;
    .tc-content{
        display: flex;
        justify-content: space-between;
        height: 100%;
    }
}
</style>


