import jsonpUtil from '../util/jsonpUtil';
const url="https://mce.mogucdn.com/jsonp/multiget/3";
const guessLikeUrl="https://list.mogujie.com/search";
const typeLeftUrl="https://mce.mogucdn.com/jsonp/multiget/3";
const typeRightUrl="https://mce.mogujie.com/jsonp/makeup/3";
const typeGoodUrl=" https://list.mogujie.com/search";
const searchUrl=" https://list.mogujie.com";
const commentUrl=" https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";
export default{
    /**
     * @param pids:string
     */
    loadDataByPids(params){
        return jsonpUtil.request(url,params)
    },
    /**
     * @param   pid:string
     * @param   sort:string
     * @param   cKey:number
     *       
     */
    loadLikeData(params){
        return jsonpUtil.request(guessLikeUrl,params)
    },
    loadTypeLeft(params){
        return jsonpUtil.request(typeLeftUrl,params)
    },
    loadTypeRightType(params){
        return jsonpUtil.request(typeRightUrl,params)
    },
    loadTypeRightGood(params){
        return jsonpUtil.request(typeGoodUrl,params)
    },
    loadTypeSearch(params){
        let url=searchUrl+"/search";
        return jsonpUtil.request(url,params);
    },
    loadHotSearch(params){
        let url=searchUrl+"/module/mget";
        return jsonpUtil.request(url,params);
    },
    loadCommentList(params){
        return jsonpUtil.request(commentUrl,params);
    }
    
}


