<template>
    <div class="shopping-page">
        <!-- 购物车标题 -->
        <div class="shop-nav">
            <i class="iconfont icon-xiangzuojiantou" @click="back()"></i>
            <span class="nav-title">购物车</span>
            <span class="nav-right" v-show="!set" @click="changeSet()">编辑
                <i class="iconfont icon-xiaoxizhongxin"></i>
            </span>
            <span class="nav-right" v-show="set" @click="changeSet()">完成</span>
        </div>
        <!-- 无商品展示区 -->
        <div class="shop-empty" v-show="flag">
            <div class="icon">
                <i class="iconfont icon-gouwuche"></i>
            </div>
            <p class="subtitle">你的购物车空空如也</p>
            <router-link tag="p" to="/home/good" class="btn">去逛逛</router-link>
        </div>
        <!-- 商品滚动展示区 -->
        <ScrollView v-if="!flag" class="sp-scroll">
            <div>
                <div class="shop-list" v-for="(v,i,n) of cartShop" :key="'shop'+n">
                    <div class="top">
                        <input type="checkbox"  v-model="shopFlag[i]" @change="selShop(i)">
                        <span>{{i}}</span>
                    </div>
                    <div class="content" v-for="(d,index,c) of v" :key="'son'+c">
                        <input type="checkbox" v-model="d.flag" @change="checkedAll(i)">
                        <img :src="d.img" alt="" @click="goToDetail(d)">
                        <div class="detail" v-show="!set">
                            <p class="title">{{ d.title }}</p>
                            <p class="option">规格：225ml ;</p>
                            <p class="pc"><b>￥ <span class="newprice">{{d.price | toFixed(2)}}</span> </b>
                                <span class="oldprice">￥{{d.orgPrice | toFixed(2) }} </span>
                                <span class="num">x{{d.num}}</span>
                            </p>
                        </div>
                        <!-- 编辑展示 -->
                        <div class="editor" v-show="set">
                        <input type="button" value="-"  class="btn" @click="setNum(d,'-')">
                        <input type="text" v-model="d.num" class="text" >
                        <input type="button" value="+" class="btn" @click="setNum(d,'+')">
                    </div> 
                    </div>     
                    
                </div>
            </div>
        </ScrollView>
        <!-- 底部 -->
        <div class="footbar">
                <div class="left">
                    <input type="checkbox" @change="changeAll()" v-model="selAll">
                    <span>全选</span>
                    <span>({{total}})</span>
                    <span class="price">￥{{price| toFixed(2)}}</span>
                </div>
                <div class="right">
                    去结算
                </div>
            </div>
    </div>
</template>
<script>
import { mapState,mapMutations } from "vuex";
export default {
    data(){
        return {
            set:false,//判断是否编辑
            selAll:false,//判断是否全选
            cartShop:{},//购物车商品信息
            shopFlag:{},//店铺选择状态表
            total:0,//选择总数
            price:0,//总价
            username:""
        }
    },
    computed:{
    //判断购物车是否为空
        flag(){      
            return Object.keys(this.cartShop).length==0;
        },
    },
    methods:{
    // 后退方法
        back(){
            this.$router.go(-1);
        },
        // 编辑和完成状态切换
        changeSet(){
            if(this.flag){
                return;
            }
            localStorage[this.username]=JSON.stringify(this.cartShop);
            this.set=!this.set;
        },
        // 编辑商品数量
        setNum(value,key){            
            if(key=='-'){
                if(value.num==1){
                    return
                }
                value.num--;
            }else{
                value.num++;
            }
            this.count();     
        },
        // 全选和全不选
        changeAll(){
            for(const key in this.shopFlag){
                 this.shopFlag[key]=this.selAll
            }
            for (const key in this.cartShop) {
                for(const index in this.cartShop[key]){
                    this.cartShop[key][index].flag=this.selAll;
                }
            }
            this.count();
        },
        // 判断是否全选和是否店铺全选
        checkedAll(name){
            this.count()
            this.selAll=false;
                this.shopFlag[name]=true;
               for(const index in this.cartShop[name]){
                   if(!this.cartShop[name][index].flag){
                       this.shopFlag[name]=false;
                       break;
                   }
               }
              for(const i in this.shopFlag){
                   if(!this.shopFlag[i]){
                       return;
                   }
               }
               this.selAll=true;
        },
        // 店铺商品全选，全不选和判断是否全选
        selShop(name){
            this.selAll=true;
            for(const key in this.cartShop[name]){
                if(key=='flag'){
                    continue;
                }
                this.cartShop[name][key].flag=this.shopFlag[name];
            }
            // 判断是否全选
            for(const index in this.shopFlag){
                if(!this.shopFlag[index]){
                    this.selAll=false;
                    break;
                }
            }
            this.count();
        },
        // 计算选中总数和总价格
        count(){
            this.total=0;
            this.price=0;
            for (const key in this.cartShop) {
               for(const index in this.cartShop[key]){
                   if(this.cartShop[key][index].flag){
                       this.total+=this.cartShop[key][index].num;
                       this.price+=this.cartShop[key][index].price*this.cartShop[key][index].num;
                   }
               }
            }
        },
        // 跳转详情页
        goToDetail(value){
            sessionStorage.good=JSON.stringify(value);
            this.$router.push("/detail");
        }
    },
    mounted(){
        this.username=sessionStorage.username
        // 判断localStorage中购物信息是否为空
        if(localStorage[this.username]){
            this.cartShop=JSON.parse(localStorage[this.username]);
            // 记录店铺选择标记
            for(let key in this.cartShop){
                this.$set(this.shopFlag,key,false);
            }
        }
    }
};
</script>
<style lang="less">
.shopping-page {
    background-color: #fafafa;
    position: relative;
    height: 100%;
    padding: 100px 0;
    .shop-nav {
        background-color: #fff;
        justify-content: space-between;
        display: flex;
        align-items: center;
        padding: 25px 27px;
        height: 100px;
        border-bottom: 1px solid #eee;
        flex-wrap: nowrap;
        position: fixed;
        top: 0;
        z-index: 888;
        width: 100%;
        > i {
            font-size: 42px;/*px*/
            color: #999;
        }
        .nav-title {
            color: #5e5e5e;
            font-size: 35px; /*px*/
        }
        .nav-right {
            color: #727272;
            font-size: 29px;/*px*/
            >i{
                margin-left:5px; 
                font-size: 35px;/*px*/
            }
        }
    }
    .shop-empty {
        position: absolute;
        width: 100%;
        text-align: center;
        top: 50%;
        color: #5e5e5e;
        transform: translate(0, -50%);
        .icon {
            width: 300px;
            height: 300px;
            background-color: #ddd5d7;
            border-radius: 100%;
            margin: 0 auto;
            display: flex;
            align-items: center;
            justify-content: center;
            > i {
                font-size: 200px;/*px*/
                color: #fff;
            }
        }
        .subtitle {
            margin: 15px 0 10px;
            font-size: 27px;/*px*/
        }
        .btn {
            display: inline-block;
            color: white;
            font-size: 29px;/*px*/
            padding:15px 60px;
            margin:10px 15px;
            background-color: #ff5777;
            border-radius: 10px;
        }
    }
    .sp-scroll{
        height: 100%;
        padding-bottom: 50px;
    }
    input{
        width: 30px;
        height: 30px;
        background-color: #fff;
        margin-right: 20px;
        outline: none;
    }
    .shop-list{
        margin-top: 40px;
        background-color: #fff;
        .top{
            height: 80px;
            border: 1px solid #ccc;
            border-right: none;
            border-left: none;
            line-height: 80px;
            padding-left: 30px;
            font-size: 30px;/*px*/
            color: #555;
        }
        .content{
            display: flex;
             padding: 30px;
             height: 270px;
             align-items: center;
             img{
                 width: 200px;
                 height: 100%;
                 margin: 0 20px;
             }
             .detail{
                 width: 400px;
                 height: 100%;
                 display: flex;
                 flex-direction: column;
                 justify-content: space-between;
                 .title{
                     color: #444;
                     font-size: 27px;/*px*/
                     height: 115px;/*px*/
                 }
                 .option{
                     color: #777;
                 }
                 .pc{
                     .newprice{
                         font-size: 32px;/*px*/
                     }
                     .oldprice{
                         color: #999;
                         text-decoration: line-through;
                     }
                     .num{
                         float: right;
                     }  
                 }
             }
             .editor{
                //   width: 400px;
                 margin-left:100px;
                 display: flex;
                 align-items: center;
                 input{
                     height: 60px; 
                     border: 1px solid #999;
                     color: #555;
                 } 
                 .btn{
                     width: 60px;
                     font-size: 40px;
                 }
                 .text{
                     width: 70px;
                     text-align: center;
                     font-size: 30px;
                 }
             }
        }
    }
    .footbar{
        position: fixed;
        width: 100%;
        bottom: 0;
        display:flex;
        justify-content: space-between;
        align-items: center;
        padding:30px 25px;
        height: 100px;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        background-color: #fff;
        .left{
            font-size: 30px;/*px*/
            color: #666;
            .price{
                color:#EA496A;
            }
        }
        .right{
            color: #fff;
            width: 200px;
            height: 60px;
            line-height: 60px;
            text-align: center;
            border-radius: 7px; 
            background-color: #FA5B7A;
            font-size: 30px;
        }

    }
}
</style>



