import NavbarComponent from './components/NavbarComponent'
import GoodComponent from './components/GoodComponent'
import ScrollView from './components/ScrollView'
const plugins={
    install(Vue,options){
        Vue.component("NavbarComponent",NavbarComponent);
        Vue.component("GoodComponent",GoodComponent);
        Vue.component("ScrollView",ScrollView);
        // 是否选择过滤器
        Vue.filter("isClick",function(value,key){
            return value==key?"active":"";
        });
        // 保留小数位过滤器
        Vue.filter("toFixed",function(value,num){
            return value.toFixed(num);
        })

    }
}
export default plugins


