<template>
    <div class="gc" @click="gotoDetail()">
        <div class="gc-img">
            <img :src="good.img" alt="">
            <slot name="logo"></slot>
        </div>
        <div class="gc-context">
            <slot name="title" ></slot>
            <div class="gc-context-detail">
                <b class="gc-context-detail-price">¥<b>{{good.price|toFixed(2)}}</b></b>
                <span class="gc-context-detail-num">{{good.cfav}} <i class="iconfont icon-shoucang"></i> </span>
            </div>
        </div>
    </div>
</template>
<script>
import { mapMutations } from "vuex";
export default {
    props:{
        good:null
    },
    methods:{
        // 跳转商品详情页
        gotoDetail(){
            // 将商品信息写入sessionStorage中
            let str=JSON.stringify(this.good);
            sessionStorage.good=str;
            this.$router.push("/detail")
        }
    }
};
</script>
<style lang="less">
    .gc{
        background-color: #fff;
        border-radius: 10px 10px 0 0;
        width: 46.5%;
        margin: 3% 0 0 2%;
        .gc-img{
            position: relative;
            img{
                border-radius: 10px;
                width: 100%;
                height: 100%;
            }
        }
        &-context{
            &-detail{
                display: flex;
                justify-content: space-between;
                &-num{
                    font-size: 25px;/*px*/
                    color: #666;
                    >i{
                        font-size: 27px;/*px*/
                        color: #aaa;
                    }
                }
            }
        }   
    }
    //首页样式
    .hch-list-item{
        .gc-img{
            height: 464px;
        }
        .gc-context{
            padding: 18px;
            .goodtitle{
                font-size: 25px;
                height: 70px;
                margin-bottom: 10px;
                position: relative;
                &:after{
                    content:"...";
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    width:38px;
                    background: linear-gradient(left,rgba(255,255,255,0.8),rgba(255,255,255,1));
                    text-align: center;
                }
            }
        }
    }
    //分类样式
    .type-good{
        .gc-img{
            height: 415px;
        }
        .gc-context{
            padding: 10px;
            .gc-context-detail-price{
                color: #FF5777;
                font-size: 27px;/*px*/
            }
            .goodtitle{
                 font-size: 23px;
                height: 30px;
                margin-bottom: 10px;
                position: relative;
                &:after{
                    content:"...";
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    width:34px;
                    background: linear-gradient(left,rgba(255,255,255,0.8),rgba(255,255,255,1));
                    text-align: center;
            }
            }
        }
    }
    //搜索样式
    .sr-wrap-goodlist{
         .gc-img{
            height: 525px;
        }
        .gc-context{
            padding: 10px;
            .gc-context-detail-price{
                font-size: 27px;/*px*/
            }
            .goodtitle{
                font-size: 23px;
                height: 80px;
                margin-bottom: 27px;
                position: relative;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                
            }
        }
    }
</style>



