<template>
    <ScrollView class="tl">
        <ul class="tl-content">
            <li  :class="v.maitKey | isClick(key)"  v-for="(v,i) of list" :key="'left'+i" @click="changeType(v.maitKey,v.miniWallkey)">
                <p class="tl-content-context">{{v.title}}</p>
            </li>
        </ul>
    </ScrollView>
</template>
<script>
import {mapState,mapActions,mapMutations} from 'vuex'
import Bscroll from "better-scroll";
export default {
    data(){
        return{
            key:"41888"//判断是否点击
        }
    },
    props:{
        list:null
    },
   methods: {
    ...mapActions(["loadRightType","loadRightGood"]),
    ...mapMutations(['setTypeId',"setSortKey","setPage"]),
    // 点击左侧导航事件
    changeType(id,wid){
        this.key=id;
        // 加载右侧上部内容
        this.loadRightType(id);
        // 加载右侧下部内容
        this.loadRightGood({sort:'pop',fcid:wid,page:1});
        // 记录导航选择项id
        this.setTypeId(wid);
        // 重置排序方式
        this.setSortKey('pop');
        this.setPage(1);
    }
  },
}
</script>
<style lang="less">
 .tl{
     width: 200px;
     background-color: #f6f6f6;
    //  height: 10rem;
    height: 100%;
     overflow: hidden;
     &-content{
         >li{
             height: 100px;/*px*/
             font-size: 28px;/*px*/
             display: flex;
             align-items: center;
             
             color: #666;
             p{
                 width: 100%;
                 text-align: center;
                 border-left: 8px solid transparent;
             }
         }
         .active{
              background-color: #fff;
              color: #FF5577;
              p{
                 border-color: #FF5577;
              }
         }
     }

 }
</style>


