import Api from '../../server/Api'
const state={
    KeyWord:"",//搜索关键字
    defaultKey:"口红",//提示字
    SortKey:'pop',//排序key
    backUrl:"",
    HotSearch:[],
    TypeSearch:[],
    HistorySearch:[],
    FiliterList:[],
}
const mutations={
    setKeyWord(state,data){
        state.KeyWord=data;
    },
    setHotSearch(state,data){
        state.HotSearch=data;
    },
    setTypeSearch(state,data){
        state.TypeSearch=data;
    },
    setdefaultKey(state,data){
        state.defaultKey=data;
    },
    setHistorySearch(state,data){
        state.HistorySearch=data;
    },
    setFiliterList(state,data){
        state.FiliterList=data;
    },
    setSortKey(state,data){
        state.SortKey=data
    },
    addTypeSearch(state,data){
        state.TypeSearch= state.TypeSearch.concat(data)
    },
    setbackUrl(state,data){
        state.backUrl=data;
    }
}
const actions={
    loadHotSearch({commit}){
        let hotParams={
            platform:'H5', 
            code:'hotWord,sketch'
        }
        Api.loadHotSearch(hotParams)
            .then(({data})=>{
                commit("setHotSearch",data.hotWord.data);
            })
    },
    loadTypeSearch({commit},params){
        let typeSearchParams={};
        if(params.page==1){
            commit("setTypeSearch",[]);
        }
        // 判断区域查询和关键字查询
        if(params.fcid){
            typeSearchParams={
                fcid:params.fcid,
                title:params.title,
                cKey:16,
                sort:params.sort?params.sort:"pop", 
                page:params.page?params.page:1
            }
        }else{
            typeSearchParams={
                q:params.q,
                cKey:46,
                 sort:params.sort?params.sort:"pop", 
                 page:params.page?params.page:1
            }
        }
        Api.loadTypeSearch(typeSearchParams)
            .then(({result})=>{
                if(typeSearchParams.page==1){
                    commit("setTypeSearch",result.wall.docs)
                }else{
                    commit("addTypeSearch",result.wall.docs)
                }
            })
    },
   loadFiliter({commit}){
        Api.loadTypeRightGood()
            .then(({result})=>{
                commit("setFiliterList",result.filter.list);
            })
   }
}
export default {
    state,mutations,actions
}


