<template>
    <ScrollView class="hs" >
        <div class="content">
            <HomeCarousel :wrap="home.SwiperList"></HomeCarousel>
            <HomeType :list="home.TypeList"></HomeType>
            <HomeRecommed :list="home.RecommendList"></HomeRecommed>
            <HomeHot :list="home.HotList"></HomeHot>
            <HomeChoosen :list="home.ChoosenList"></HomeChoosen>
        </div>
    </ScrollView>    
</template>
<script>
// 加载组件
import HomeCarousel from './HomeCarousel';
import HomeType from './HomeType';
import HomeRecommed from './HomeRecommed';
import HomeChoosen from './HomeChoosen';
import HomeHot from './HomeHot';
import { mapState,mapMutations,mapActions } from "vuex";
export default {
    data(){
        return {
            pulldown:true
        }
    },
    computed:{
         ...mapState(["home"])
    },
    components:{
        HomeCarousel,HomeType,HomeRecommed,HomeHot,HomeChoosen
    },
    methods:{
        ...mapActions(["loadHomeData"]),
       
    },
    mounted() { 
      this.loadHomeData();
    },
}
</script>
<style lang="less">
    .hs{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
</style>



