import Api  from '../../server/Api'
const state={
    // 评论列表信息
    CommentList:{}
}
const mutations={
    setCommentList(state,data){
        state.CommentList=data;
    }
}
const actions={
    loadCommentList({commit},id){
        let commentParams={
            type:'1',
            itemId:id,
            appPlat:'m'
        }
        Api.loadCommentList(commentParams)
            .then(({data})=>{
                commit("setCommentList",data);
            })
    }
}
export default{
    state,mutations,actions
}


