<template>
    <div class="container">
        <!-- 默认展示导航 -->
        <div v-if="!flag" class="nb">
            <slot></slot>
            <div  class=" nb-form">
                <i class="iconfont icon-sousuo  nb-form-icon"></i>
                <input type="text" class=" nb-form-search" @focus="setFlag()" v-model="search.KeyWord">
            </div>
            <router-link to="/shop" class="iconfont icon-gouwuche" tag="i"></router-link>
        </div>
        <!-- 搜索时导航 -->
        <div v-if="flag" class="nb-search nb">
            <i class="iconfont icon-xiangzuojiantou" @click="back()"></i>
            <input type="text" class=" nb-search-input" autofocus v-model="search.KeyWord" :placeholder="search.defaultKey">
            <span @click="toSearch()">搜索</span>
        </div>
        <!-- 搜索提示区 -->
        <transition  enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
         <div class=" search-bottom" v-if="flag">
                <div class=" search-bottom-content">
                    <div class=" search-bottom-content-title">
                        <i class="iconfont icon-sousuo"></i>
                        <span>历史搜索</span>
                        <i class="iconfont icon-lajixiang right" @click="clearHistory()"></i>
                    </div>
                    <div class=" search-bottom-content-block">
                        <p v-show="!localEmpty" class="info">暂无搜索记录</p>
                        <div v-show="localEmpty" v-for="(v,i) of history" :key="'history'+i" class="info-block" @click="toSearch(v)">{{v}}</div>
                    </div>
                </div>
                <div  class=" search-bottom-content">
                    <div class=" search-bottom-content-title">
                        <i class="iconfont icon-shoucang"></i>
                        <span>热门搜索</span>
                    </div>
                    <div class=" search-bottom-content-block">
                        <div v-for="(v,i) of search.HotSearch" :key="'hot'+i" class="info-block" @click="toSearch(v.frontword)" :style="{color:v.color}">{{v.frontword}}</div>
                    </div>
                </div>
               </div> 
          </transition>
        </div>

   
</template>
<script>
import {mapState,mapMutations,mapActions} from 'vuex'
export default {
  data() {
    return {
      flag: false,//判断搜索框是否显示
      history:[],//历史信息
      num:0,
    };
  },
  computed:{
    ...mapState(["search"]),
  //  判断历史搜索是否为空
    localEmpty(){
      return this.history.length
    }
  },
  methods: {
    ...mapMutations(["setKeyWord","setdefaultKey","setHistorySearch"]),
    ...mapActions(["loadHotSearch",'loadTypeSearch']),
    // 显示搜索框方法
    setFlag() {
      this.setKeyWord("");
      this.flag = true;
    },
    // 读取localStorage方法并赋值
     loadStorage(){
      let arr=[];
      // 判断Storage是否为空
       if(localStorage.history!=""){
        arr=localStorage.history.replace(/,$/,"").split(",");
       }
      this.history=arr;
    },
    // 显示搜索结果
    toSearch(key){

      this.search.SortKey='pop';
      this.flag = false;
      // 判断关键字，关键字为空使用默认提示
      let word=key?key:this.search.KeyWord?this.search.KeyWord:this.search.defaultKey;
      // 判断历史信息是否重复
      if( localStorage.history.indexOf(word)==-1){
        localStorage.history=localStorage.history+word+",";
      }
      this.setKeyWord(word);
      this.setdefaultKey(word);
      this.loadStorage();
      // 跳转至搜索结果
      this.$router.push("/search?q="+word);
    },
    // 清除历史记录方法
    clearHistory(){
      if(this.localEmpty){
        localStorage.history="";
        this.loadStorage();
      }
    },
    // 后退方法
    back(){
      this.flag=false;
    }
  },
  mounted(){
    this.loadStorage();
    this.loadHotSearch();
  }
};
</script>
<style lang="less">
.container{
    overflow: hidden;
}
.nb {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  height: 100px; /*px*/
  width: 100%;
  padding: 25px 27px;
  color: #666;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  z-index: 999;
  > i {
    font-size: 50px;/*px*/
    display: inline-block;
    width: 60px;/*px*/

  }
  &-form {
    width: 100%;
    position: relative;
    margin: 0 30px;
    &-icon {
      position: absolute;
      top: 15px;
      left: 20px;
      font-size: 30px; /*px*/
    }
    &-search {
      width: 100%;
      outline: none;
      background-color: #eee;
      color: #999;
      border: none;
      font-size: 28px; /*px*/
      border-radius: 8px;
      height: 60px;
      padding: 10px;
      padding-left: 60px;
      line-height: 60px;
    }
  }
}
.nb-search {
  &-input {
    margin-left: 30px;
    margin-right: 20px;
    width: 100%;
    outline: none;
    border: 1px solid #ccc;/*px*/
    font-size: 28px; /*px*/
    border-radius: 8px;
    height: 50px;
    padding: 10px;
    padding-left: 20px;
    line-height: 50px;
    &:focus {
      border-color: #ff5777;
    }
  }
  
  span {
    display: inline-block;
    width: 12%;
    font-size: 28px; /*px*/
    white-space: nowrap;
    margin-right: 10px;
    text-align: right;
  }
}
.search-bottom {
  position: absolute;
  top: 100px;
  z-index: 998;
  background-color: #fff;
  height: 100%;
  overflow: hidden;
  &-content {
    padding: 30px;
    &:first-child {
      border-bottom: 1px solid #ccc;/*px*/
    }
    &-title {
      font-size: 25px; /*px*/
      color: #999;
      .right {
        float: right;
      }
    }
    &-block {
      .info{
        padding:25px 0 0 35px;
        color: #666;
      }
      .info-block {
        color: #666;
        border: 1px solid #ccc;/*px*/
        display: inline-block;
        padding: 0 20px;
        margin: 35px 0 0 30px;
        font-size: 25px; /*px*/
        border-radius: 5px;
        line-height: 50px; /*px*/
        height: 50px; /*px*/
      }
      .color{
        color: red;
      }
    }
  }
}
</style>



