<template>
    <div class="lg">
        <div class="wait" v-show="!flag">
            <p>请登录</p>
        </div>
        <div v-show="flag" class="show">
         <i class="back iconfont icon-xiangzuojiantou" @click="back()"></i>
            <div class="center">
                <h2 class="title">
                    登录/注册
                </h2>
                <div class="table">
                    <div class="topbtn">
                        <div :class="'login'|Click(click)" @click="changePage('login')">登录</div>
                        <div :class="'regin'|Click(click)" @click="changePage('regin')">注册</div>
                    </div>
                    <div class="bottom">
                        <div class="login" v-show="click=='login'">
                            <input type="text" placeholder="请输入用户名" v-model="username">
                            <input type="password" placeholder="请输入密码" v-model="userpass">
                            <input type="button" value="登录" class="login-btn" @click="login()">
                        </div>
                        <div class="login" v-show="click=='regin'">
                            <input type="text" placeholder="请输入用户名" v-model="username">
                            <input type="password" placeholder="请输入密码" v-model="userpass">
                            <input type="button" value="注册" class="login-btn" @click="regin()">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            flag:false,
            click:"login",
            username:"",
            userpass:""
        }
    },
    filters:{
        Click(value,flag){
            let key=(value==flag?"active":"unactive");
            return key;
        }
    },
    methods:{
        changePage(value){
            this.username="";
            this.userpass="";
            this.click=value;
        },
        login(){
            let users=JSON.parse(localStorage.users);
            if(this.username in users){
                let user=users[this.username];
                if(user.userpass==this.userpass){
                    sessionStorage.username=this.username;
                    this.$router.push("/")
                }else{
                    alert("密码错误")
                }
            }else{
                alert("用户不存在")       
            }
        },
        regin(){
             let user;
            if(!localStorage.users){
                user={};
                
            }else{
                user=JSON.parse(localStorage.users);
                if(this.username in user){
                    alert("用户已存在");
                    return 
                }
            }
            user[this.username]={
                username:this.username,
                userpass:this.userpass,
            }
            localStorage.users=JSON.stringify(user)

            alert("注册成功");
        },
        back(){
            this.$router.go(-1)
        }

    },
    mounted() {
        setTimeout(()=>{
            this.flag=true;
        },800)
    },
}
</script>
<style lang="less">
 .lg{
     height: 100%;
    //  background-color: #fff;
    
     .wait{
         p{
             width: 400px;
             height: 100px;
             background-color: rgba(0,0,0,0.3);
             text-align: center;
             line-height: 100px;
             font-size: 30px;
             color: #fff;
             position:absolute;
             top: 50%;
             left: 200px;
             margin-top: -50px ;  
         }
     }
     .show{
         width: 100%;
         height: 100%;
         background-color: #fff;
          .back{
            position: fixed;
            font-size: 40px;/*px*/
            top:40px;
            left: 30px;
            display: block;
            height: 60px;/*px*/
            width: 60px;/*px*/
            background-color:rgba(0,0,0,0.8);
            color: #fff;
            text-align: center;
            line-height: 60px;/*px*/
            border-radius: 200%;
            z-index: 888;
            }
         .center{
             width: 100%;
             text-align: center;
             padding-top: 30%;
             .title{
                 margin-bottom: 50px;
                 font-size: 40px;
                 color: #333;
             }
             .table{
                 width: 90%;
                //  border: 1px solid #ccc;
                 margin: 0 auto;
                     overflow: auto;
                 .topbtn{
                     width: 100%;
                     display: flex;
                     justify-content: space-between;
                     overflow: auto;

                     >div{
                         width: 49%;
                         text-align: center;
                         height: 100px;
                         line-height: 100px;
                         font-size: 30px;
                         font-weight: 200;
                         color: #777;
                         
                     }
                     .active{
                         border: 2px solid #999;/*px*/
                         border-bottom: 0px solid #999;/*px*/
                         border-radius: 10px 10px 0 0 ;
                         border-left-width: 1px;
                         color: #ff5777;
                     }
                     .unactive{
                         border-bottom: 1px solid #999;/*px*/
                     }

                 }
                 .bottom{
                    height: 600px;
                    border: 1px solid #999;/*px*/
                    border-top:0px solid #999 ;/*px*/
                    input{
                        display: block;
                        border: 1px solid #999;/*px*/
                        border-radius: 10px;
                        width: 60%;
                        height: 70px;
                        margin: 80px auto;
                        outline: none;
                        font-size: 30px;
                        padding-left: 20px;
                    }
                    .login-btn{
                        background-color: #ff5777;
                        border: 0px solid #fff;/*px*/
                        color: #fff;
                    }
                    
                 }
             }
         }
     }
 }
</style>



