import Vue from 'vue'
import Router from 'vue-router'

import HomeView from '../components/HomeView'
import HomeScroll from '../components/Home/HomeScroll'
import TypeComponent from '../components/Type/TypeComponent'
import SearchResult from '../components/Search/SearchResult'
import DetailComponent from '../components/Search/DetailComponent'
import ShopComponent from '../components/Shop/ShopComponent'
import AboutMe from '../components/AboutMe/AboutMe'
import LoginPage from '../components/AboutMe/LoginPage'
Vue.use(Router)


export default new Router({
  mode:"history",
  linkActiveClass:"link-active",
  linkExactActiveClass:"exact-active",
  routes: [
   {path:"/",redirect:"/home"}, 
   {path:"/home",component:HomeView,children:[
     {path:"/",redirect:"good"},
     {path:"good",component:HomeScroll},
     {path:"type",component:TypeComponent}
   ]},
   {path:"/search",component:SearchResult},
   {path:"/detail",component:DetailComponent},
   {path:"/shop",component:ShopComponent},
   {path:"/aboutme",component:AboutMe,beforeEnter(to,from,next){
     if(sessionStorage.username){
       next()
     }else{
       next("/login");
     }
   }},
   {path:"/login",component:LoginPage},
  ]
})



