<template>
    <div class="trb">
        <div class="trb-nav">
            <ul class="trb-nav-filter">
                <li :class="v.sortKey | isClick(type.SortKey)" v-for="(v,i) of type.RightFiliter" :key="'filter'+i" @click="filiterGood(v.sortKey)">{{v.title}}</li>
            </ul>
        </div>
        <div class="trb-list">
            <div class="trb-list-content">
                <GoodComponent class="type-good" v-for="(good,i) of type.RightGoodList" :key="'good'+i" :good="good">
                    <div class="selled" slot="logo">
                        <span>已售 <span class="num">{{good.sale}}</span> 件</span>
                    </div>
                    <p class="goodtitle" slot="title">{{good.title}}</p>
                </GoodComponent>
            </div>
        </div>
    </div>    
</template>
<script>
import {mapState,mapActions,mapMutations} from 'vuex'
export default {
    computed:{
        ...mapState(["type"])
    },
    methods:{
        ...mapActions(["loadRightGood"]),
        ...mapMutations(["setSortKey","setPage"]),
        // 过滤排序商品方法
        filiterGood(key){
            this.setPage(1);
            this.setSortKey(key);
            // 重新加载商品信息
            this.loadRightGood({sort:key,fcid:this.type.TypeId,page:1})
        }
    },
    mounted() {
        // 加载商品信息
        this.loadRightGood()
    },
}
</script>
<style lang="less">
    .trb{
        .trb-nav{
            border-bottom: 1px solid #eee;
            .trb-nav-filter{
                display: flex;
                height: 100px;
                align-items: center;
                font-size: 28px;/*px*/
                color: #333;
                >li{
                    width: 33%;
                    text-align: center;
                    border-right: 0.02rem solid #eee;
                }
                .active{
                    color: #ef4562;
                }
            }
        }
        &-list{
            &-content{
                display: flex;
                // justify-content: space-around;
                flex-wrap: wrap;
            }
        }
        .selled{
            position: absolute;
            bottom: 30px;
            color: #fff;
            font-size: 25px;/*px*/
            padding: 0 0.3rem 0 0.2rem;
            line-height: 38px;/*px*/
            z-index: 999;
            background-image: linear-gradient(left,rgba(0,0,0,0.5),rgba(0,0,0,0) )
        }
        
    }
</style>



