<template>
    <div class="hr">
        <div class="hr-title">
            <p>超实惠－促销直达</p>
        </div>
        <div class="hr-content">
            <div class="recommend-item" v-for="(v,i) of list" :key="i" :class="'item-'+i">
                <div class="context">
                    <h1 class="context-title">{{ v.title }}</h1>
                    <p class="subtitle">{{v.viceTitle | removeTitle() }}</p>
                </div>
                <div class="image">
                    <img :src="v.image" alt="">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        list:null
    },
    filters: {
        removeTitle(value) {
            return value.replace(/[{}]/g, "");
        }
    },
    mounted() {
       
    }
};
</script>
<style lang="less">
.hr {
    background-color: #fff;
    &-title {
        font-size: 30px; /*px*/
        height: 90px; /*px*/
        line-height: 90px;
        text-indent: 40px;
    }
    &-content {
        overflow: hidden;
        .recommend-item {
            float: left;
            img {
                width: 140px;
                height: 138px;
            }
            .context {
                .context-title {
                    font-size: 30px; /*px*/
                    color: #333;
                    line-height: 40px;
                }
                .subtitle {
                    color: #ff5f7e;
                    font-size: 25px; /*px*/
                    line-height: 33px;
                }
            }
        }
        .item {
            &-0 {
                width: 50%;
                text-align: center;
                height: 298px;
                border: 1px solid #e5e5e5;/*px*/
                border-left: 0px solid #e5e5e5;/*px*/
                border-bottom: 0px solid #e5e5e5;/*px*/
                padding-top: 35px;
                .image {
                    img {
                        width: 180px;
                        height: 180px;
                    }
                }
            }
            &-1,
            &-2 {
                width: 50%;
                height: 150px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-top: 1px solid #e5e5e5;/*px*/
                padding-left: 20px;
            }
            &-3,
            &-4,
            &-5 {
                width: 33.2%;
                height: 250px;
                padding-top: 20px;
                text-align: center;
                border-top: 1px solid #e5e5e5;/*px*/
                border-right: 1px solid #e5e5e5;/*px*/
            }
            &-5 {
                border-right: 0px solid #e5e5e5;/*px*/
            }
        }
    }
}
</style>





