<template>
    <div class="trt" >
        <div class="banner" v-for="(v,i) of type.RightBanner" :key="'banner'+i">
            <img :src="v.image" >
        </div>
        <div class="rt-item" v-for="(v,i) of type.RightType" :key="'cate'+i" @click="goToGood(v.link,v.title)">
            <img :src="v.image" alt="">
            <p>{{v.title}}</p>
        </div>
    </div>    
</template>
<script>
import {mapState} from 'vuex'
export default {
    computed:{
        ...mapState(["type"])
    },
    methods:{
        // 跳转至区域搜索
        goToGood(link,title){ 
            // 截取link中的fcid   
            let reg=/fcid.[0-9]*/;
            let str=link.match(reg)[0];
            this.$router.push("/search?"+str+"&title="+title);
        
        }
    }
}
</script>
<style lang="less">
    .trt{
        display: flex;
        flex-wrap: wrap;
        // justify-content: space-between;
        .banner{
            width: 100%;
            margin-top: 10px;
            margin-bottom: 20px;
            img{
                width: 100%
            }
        }
        .rt-item{
            text-align: center;
            width: 33%;
            height: 200px;
            margin: 17px 0;
            font-size: 25px;/*px*/
            img{
                width: 150px;
                height: 150px;
            }
        }
    }
</style>



