import Api from '../../server/Api'
const state={
    LeftList:[],
    RightBanner:[],
    RightType:[],
    RightFiliter:[],
    RightGoodList:[],
    TypeId:41888,
    SortKey:'pop',
    Page:1
}
const mutations={
    setLeftList(state,data){
        state.LeftList=data;
    },
    setRightBanner(state,data){
        state.RightBanner=data;
    },
    setRightType(state,data){
        state.RightType=data;
    },
    setRightFiliter(state,data){
        state.RightFiliter=data;
    },
    setRightGoodList(state,data){
        state.RightGoodList=data;
    },
    setTypeId(state,data){
        state.TypeId=data;
    },
    setSortKey(state,data){
        state.SortKey=data
    },
    addRightGoodList(state,data){
        state.RightGoodList=state.RightGoodList.concat(data);
    },
    setPage(state,data){
        state.Page=data
    }
}
const actions={
    // 加载左侧数据
    loadLeftData({commit}){
        let leftParams={
            appPlat:"m",
            pids:"41789"
        }
       
        Api.loadTypeLeft(leftParams)
            .then(({data})=>{
                commit("setLeftList",data[41789].list)
            })
     
    },
    // 加载右侧上部数据
    loadRightType({commit},pid=41888){
        let rightTypeParams={
            appPlat:"m",
            pid
        }
        Api.loadTypeRightType(rightTypeParams)
        .then(({data})=>{
            commit("setRightType",data.categoryNavigation.list);
            commit("setRightBanner",data.bannerHead.list)
        })
        
    },
    // 加载右侧下部数据
    loadRightGood({commit},params={sort:'pop',fcid:'10062603',page:1}){
        // commit("setRightFiliter",[])
        if(params.page==1){
            commit("setRightGoodList",[])
        }
        let rightGoodParams={
            fcid:params.fcid,
            sort:params.sort,
            page:params.page,
            cKey:'h5-cube'
        };
        Api.loadTypeRightGood(rightGoodParams)
            .then(({result})=>{
                if(rightGoodParams.page==1){
                    commit("setRightFiliter",result.sortFilter)
                    commit("setRightGoodList",result.wall.docs)
                }else{
                    console.log(result);
                    commit("addRightGoodList",result.wall.docs)
                }
            })
    }
}
export default {
    state,actions,mutations
}



