<template>
<div class="dc" ref="wrapper4">
    <div class="dc-scroll" v-if="isLoad">
        <div class="banner">
            <img :src="session.GoodDetail.img">
        </div>
        <div class="top">
            <div class="title">
                {{session.GoodDetail.title}}
            </div>
            <div class="price">
                <span>￥</span>
                <span class="num">{{session.GoodDetail.price|toFixed(2)}}</span>
                <div class="right">
                    <p class="oldprice">￥  <span>{{session.GoodDetail.orgPrice|toFixed(2)}}</span></p>
                    <div class="tag">{{session.GoodDetail.price/session.GoodDetail.orgPrice*10 |toFixed(1)}}折</div>
                </div>
            </div>
            <p class="yunfei">免邮费</p>
            <ul class="promise-list">
                <li class="promise-item">
                    <i class="iconfont icon-gou"></i>
                    <span>72小时发货</span>
                </li>
                <li class="promise-item">
                    <i class="iconfont icon-gou"></i>
                    <span>7天无理由退货</span>
                </li>
                <li class="promise-item">
                    <i class="iconfont icon-gou"></i>
                    <span>退货补运费</span>
                </li>
              
            </ul>
        </div>
        <div class="size">
            <div>
                <span>请选择：尺码</span>
            </div>
        </div>
        <div class="comment" v-if="isAjax">
            <div class="sell">
                <span>买家评论 {{good.CommentList.total}} | 销量 {{session.GoodDetail.cfav}}</span>
            </div>
            <div class="comment-tag" v-if="good.CommentList.rateTags.length">
                <div class="tagitem" v-for="(v,i) of good.CommentList.rateTags" :key="'rate'+i">{{v.value}}({{v.num}})</div>
            </div>
            <div class="comment-list">
                <div class="comment-item" v-for="(v,i) of good.CommentList.list" :key="i+'com'">
                    <div class="com-top">
                        <img :src="v.user.avatar" class="userimg">
                        <span class="username">{{v.user.uname}}</span>
                    </div>
                    <div class="com-bottom">
                        <p class="com-context">{{v.content}}</p>
                        <span class="time">{{v.time}}</span>
                    </div>
                </div>
            </div>
        </div>
         </div>
         <div class="addsucc" v-show="suc">
            <p>添加成功</p>
        </div>
        <i class="back iconfont icon-xiangzuojiantou" @click="back()"></i>
        <div class="good-footbar">
            <div class="foot-left">
                <div class="left-icon">
                    <i class="iconfont icon-shoucang"></i>
                    <p>收藏</p>
                </div>
                <div class="left-icon">
                    <i class="iconfont icon-shouye"></i>
                    <p>店铺</p>
                </div>
            </div>
            <div class="foot-right">
                <div class="right-btn addshop" @click="addShop()">
                    <span>加入购物车</span>
                </div>
                <div class="right-btn buynow">
                    <span>立即购买</span>
                </div>
            </div>
        </div>
   
    </div>
</template>
<script>
import Bscroll from 'better-scroll'
import { mapState,mapActions,mapMutations } from "vuex";
export default {
    data(){
        return {
            
            suc:false,//判断商品是否添加成功
            session:{},//接收商品信息
            username:""
        }
    },
    computed:{
        ...mapState(["good"]),
        // 商品信息是否加载
        isLoad(){
            return Object.keys(this.session).length
        },
        // 评论列表是否加载
        isAjax(){
            return this.good.CommentList&&Object.keys(this.good.CommentList).length
        }
    },
     methods: {
         ...mapActions(["loadCommentList"]),
         ...mapMutations(["setShopList"]),
        BscrollInit() {
            this.$nextTick(() => {
                this.scroll = new Bscroll(this.$refs.wrapper4, {
                     click:true
                });
            });
        },
        // 回退方法
        back(){
            this.$router.go(-1);
        },
        // 加入购物车方法
        addShop(){
            if(!sessionStorage.username){
                this.$router.push("/login");
                return;
            }
            let data=this.session.GoodDetail;
            // 判断是否有店铺名
            if(!data.shopSeller){
                data.shopSeller="其他";
            }
            this.suc=true;
            // 提示框消失
            setTimeout(()=>{
                this.suc=false;
            },1000)
            // 判断localStorage是否有值
            if(!localStorage[this.username]){
                //没有初始值时
                let obj={};
                obj[data.shopSeller]={};
                obj[data.shopSeller][data.iid]=data;
                this.$set( obj[data.shopSeller][data.iid],'flag',false);
                this.$set( obj[data.shopSeller][data.iid],'num',1);
                obj=JSON.stringify(obj);
                localStorage[this.username]=obj;
            }else{
                // 有初始值时
                let list=JSON.parse(localStorage[this.username]);
                // 判断有无该商铺信息
                if(data.shopSeller in list){
                    // 有商铺信息
                    // 判断有无该商品信息
                    if(data.iid in list[data.shopSeller]){
                        // 有该商品信息
                        list[data.shopSeller][data.iid].num++
                    }else{
                        // 无该商品信息
                        list[data.shopSeller][data.iid]=data;    
                        this.$set( list[data.shopSeller][data.iid],'num',1);
                        this.$set( list[data.shopSeller][data.iid],'flag',false);
                    }
                }else{
                    // 无商铺信息
                    list[data.shopSeller]={};
                    list[data.shopSeller][data.iid]=data;
                    this.$set( list[data.shopSeller][data.iid],'num',1);
                    this.$set( list[data.shopSeller][data.iid],'flag',false);
                }
                list=JSON.stringify(list);
                localStorage[this.username]=list;
            }
        }
    },
    mounted() {
        this.username=sessionStorage.username;
        // 读取sessionStorage中存储的商品信息并赋值
        this.$set(this.session,"GoodDetail",JSON.parse(sessionStorage.good))
        // 加载评论列表
        this.loadCommentList(this.session.GoodDetail.iid)
        this.BscrollInit();
    }
};
</script>
<style lang="less">
    .dc{
        padding-bottom: 100px;
        height: 100%;
        position: relative;
        // 加入商品成功弹框
        .addsucc{
            position: fixed;
            background-color: rgba(0,0,0,0.5);
            color: #fff;
            width: 250px;
            height: 100px;
            top: 500px;
            left: 250px;
            z-index: 998;
            font-size: 40px;/*px*/
            line-height: 100px;/*px*/
            text-align: center;
            border-radius: 10px;
        }
        .back{
                position: fixed;
                font-size: 40px;/*px*/
                top:30px;
                left: 20px;
                display: block;
                height: 60px;/*px*/
                width: 60px;/*px*/
                background-color:rgba(0,0,0,0.8);
                color: #fff;
                text-align: center;
                line-height: 60px;/*px*/
                border-radius: 200%;
                z-index: 888;
            }
            .dc-scroll{
                padding-bottom: 50px;
            }
        .banner{
            background-color: #fff;
            width: 100%;
            height: 800px;
            position: relative;
           
            img{
                width: 100%;
                height: auto;
            }
        }
        .top{
            padding: 0 30px;
            background-color: #fff;
            .title{
                padding: 10px 0;
                color: #424242;
                font-size: 35px;/*px*/
                line-height: 45px;/*px*/
            }
            .price{
                display: flex;
                align-items: center;
                .num{
                    font-size: 65px;/*px*/
                    margin-right: 8px;
                }
                .right{
                    
                    .oldprice{
                        font-size: 22px;/*px*/
                        text-decoration: line-through;
                        color: #999;
                        height: 50%;
                    }
                    .tag{
                        height: 50%;
                        color: rgb(255, 34, 85);
                        background-color:rgb(255, 232, 238);
                        font-size: 22px;
                        text-align: center;
                    }
                }
                
            }
            .yunfei{
                margin-top: 10px;
                padding: 10px 0;
                border-bottom: 1px solid #eee;
                color: #999;
                font-size: 24px;/*px*/
            }
            .promise-list{
                position: relative;
                padding: 15px 0;
                .promise-item{
                    display: inline-block;
                    margin-right: 25px;
                    i{
                        color:rgb(255, 34, 85);
                    }
                    span{
                        font-size: 22px;/*px*/
                        color: #666;
                    }
                }
            }
           
        }
        .size{
            padding: 0 30px;
            background-color: #fff;
            color: #666;
            font-size: 24px;/*px*/
            height: 90px;/*px*/
            line-height:90px;/*px*/
            margin: 20px 0;
            >div{
                position: relative;
                
            }
        }
        .comment{
            padding: 0 30px;
            background-color: #fff;
            .sell{
                height: 70px;
                line-height: 70px;
                position: relative;
                font-size: 24px;/*px*/
                color: #999;
            }
            .comment-tag{
                height: 140px;
                .tagitem{
                    color: #f25;
                    background-color: #ffe8ee;
                    height: 48px;
                    line-height: 48px;
                    padding: 0 12px;
                    margin: 10px 10px 0 0;
                    display: inline-block;
                    font-size: 24px;
                    text-align: center;
                }
            }
            .comment-list{
                .comment-item{
                    margin-top: 10px;
                    padding: 30px 0;
                    border-bottom: 1px solid #eee;
                    .com-top{
                        .userimg{
                            border-radius: 200px;
                            vertical-align: middle;
                            margin-right: 10px;
                            width: 60px;
                            height: 60px;
                        }
                        .username{
                            font-size: 27px;/*px*/
                        }
                    }
                    .com-bottom{
                        padding:20px 0;
                        .com-context{
                            color: #727272;
                            font-size: 27px;/*px*/
                            line-height: 40px;/*px*/
                            margin-bottom: 10px;
                        }
                        .time{
                            color: #999;
                            font-size: 24px;/*px*/
                        }
                    }
                }
            }
        }
        .good-footbar{
            position: fixed;
            bottom: 0;
            display: flex;
            width: 100%;
            background-color: #fff;
            height: 100px;
            align-items: center;
            border-top: 1px solid #eee;
            .foot-left{
                display: flex;
                width: 34%;
                align-items: center;
                .left-icon{
                    width: 50%;
                    text-align: center;
                    color: #666;
                    height: 100px;
                    border-right: 1px solid #ddd;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                >i{
                    font-size: 35px/*px*/;
                    line-height: 45px;/*px*/
                }
                >p{
                    font-size: 23px;/*px*/
                }
                }
            }
            .foot-right{
                display: flex;
                width: 66%;
                .right-btn{
                    width: 50%;
                    text-align: center;
                    height: 100px;
                    line-height:100px;
                    font-size: 25px;/*px*/
                }
                .addshop{
                    color: #ff5777;
                    background-color:#ffe6e8;
                }
                .buynow{
                    color: #fff;
                    background:linear-gradient(90deg,#ff5777,#ff468f);
                }
            }
        }

    }
</style>




