<template>
    <div class="hv">
        <div class="hv-body" >
            <NavbarComponent class="hv-body-nc">
                <i class="iconfont icon-xiaoxizhongxin"></i>
            </NavbarComponent>
            <router-view></router-view>
            <FootBar></FootBar>
        </div>
    </div>
</template>
<script>
import FootBar from './FootBar'
export default {
    data(){
        return {
            flag:true
        }
    },
    components:{
        FootBar
    }
}
</script>
<style lang="less">
.hv{
    height: 100%;
    width: 100%;
    &-body{
        width: 100%;
        height: 100%;
        padding: 100px 0;/*px*/
        
    }
    }
</style>



