<template>
    <ScrollView class="tr" :pullup="true" @scrollToEnd="loadData" :data="type.RightGoodList">
        <div>
            <div class="tr-top">
                <TypeRightTop ></TypeRightTop>
            </div>
            <div class="tr-bottom">
                <TypeRightBottom></TypeRightBottom>
            </div>
        </div>
    </ScrollView>    
</template>
<script>
import TypeRightTop from './TypeRightTop';
import TypeRightBottom from './TypeRightBottom';
import Bscroll from "better-scroll";
import { mapState,mapActions,mapMutations } from "vuex";
export default {
    computed:{
        ...mapState(["type"])
    },
    components:{
        TypeRightTop,TypeRightBottom
    },
    methods:{
        ...mapActions(["loadRightGood"]),
        ...mapMutations(["setPage"]),
         loadData(){
             this.setPage(this.type.Page+1);
             let parmas={
                 sort:this.type.SortKey,
                 fcid:this.type.Typeid,
                 page:this.type.Page
             }
            this.loadRightGood(parmas);
        }
    }
}
</script>
<style lang="less">
    .tr{
        width: 100%;
        background-color: #fff;
        height: 100%;
        overflow: hidden;
        &-top{
            padding: 20px ;
            padding-bottom: 0;
        }      
    }
</style>



