<template>
<div class="hc">
<swiper :options="swiperOption" ref="mySwiper">
    <swiper-slide v-for="(v,i) of wrap" :key="'wrap'+i" >
        <img class="hc-slide-img" :src="v.image_800">
    </swiper-slide> 
    <div class="swiper-pagination" id="pag" slot="pagination"></div>
    
</swiper>  
</div>
</template>
<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import Api from '../../server/Api'
export default {
  data() {
    return {
      swiperOption: {
        autoplay: true,
        pagination: {
          el: ".swiper-pagination"
        }
      },
    };
  },
  props:{
    wrap:null
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted() {
   
  },

};
</script>
<style lang="less">
.hc {
  &-slide-img {
    width: 100%;
    height: 100%;
  }
  .swiper-pagination-bullet-active{
    background:#d43e2e
  }
  #pag {
    text-align: left;
    margin-left: 20px;
  }
  .swiper-pagination-bullets .swiper-pagination-bullet{
    margin-right: 20px;
  }
  span{
    width: 14px;
    height: 14px;
  }
}
</style>



