<template>
    <div class="hh">
        <div class="hh-top">
            <p class="hh-toptitle"> 热门分类</p>
        </div>
        <div class="hh-list">
            <div class="hh-list-item" v-for="(v,i) of list" :key="i">
                <img :src="v.image">
                <p class="hh-list-item-title">{{v.title}}</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        list:null
    },
    mounted() {
    }
};
</script>
<style lang="less">
.hh {
    margin-top: 20px;
    background-color: #fff;
    &-top {
        background: url("/static/img/xiu.png") no-repeat;
        background-position: center center;
        background-size: 60%;
        padding: 20px 0;
        line-height: 80px;
        text-align: center;
        font-size: 32px; /*px*/
        margin-bottom: 20px;
    }
    .hh-list {
        display: flex;
        // justify-content: space-between;
        flex-wrap: wrap;
        background-color: #fff;
        margin-bottom: 20px;
        &-item {
            width: 25%;
            text-align: center;
            margin-bottom: 25px;
            img {
                width: 130px;
                height: 130px;
                border-radius: 100%;
            }
            &-title {
                font-size: 26px; /*px*/
                color: #777;
            }
        }
    }
}
</style>



