<template>
    <div class="ht">
         <div class="ht-item" v-for="(v,i) of list" :key="'type'+i">
        <img :src="v.image">
        <p class="ht-item-title">{{ v.title }}</p>
    </div>
    </div>
</template>
<script>
export default {
    props:{
        list:null
    },
}
</script>
<style lang="less">
    .ht{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        background-color: #fff;
        margin-bottom: 20px;
        &-item{
            width: 20%;
            text-align: center;
            margin-top: 25px;
            img{
                width: 100px;
                height: 100px;
                border-radius: 100%;
            }
            &-title{
                font-size: 25px;/*px*/
            }
        }
    }
</style>



