<template>
    <div class="sr">
        <NavbarComponent class="sr-navbar">
            <i class="iconfont icon-xiangzuojiantou" @click="back()"></i>
        </NavbarComponent>
        <ul class="sr-filter">
            <li :class="v.sort | isClick(search.SortKey) " v-for="(v,i) of search.FiliterList" :key="'filiter'+i" @click="filiterGood(v.sort)">{{v.title}}</li>
        </ul>
        <ScrollView class="sr-wrap" ref="wrapper3" :pullup="true" @scrollToEnd="loadData" :data="search.TypeSearch">
            <div v-if="!search.TypeSearch.length" class="error">
                <p>抱歉，没有查找到该商品信息</p>
            </div>
            <div class="sr-wrap-goodlist"  v-if="search.TypeSearch.length">
                <GoodComponent v-for="(good,i) of search.TypeSearch" :key="'good'+i" :good="good">
                    <div class="goodtitle" slot="title">
                        <div class="title-block"  v-for="(v,i) of good.props" :key="'title'+i">{{v}}</div>
                    </div>
                </GoodComponent>
            </div>
        </ScrollView>
    </div>
</template>
<script>
import Bscroll from "better-scroll";
import {mapState,mapActions,mapMutations} from 'vuex'

export default {
    data(){
        return {
            page:1,
        }
    },
    computed:{
        ...mapState(["search"])
    },
    methods: {
        ...mapActions(['loadTypeSearch',"loadFiliter"]),
        ...mapMutations(['setbackUrl']),
        // 排序商品方法
        filiterGood(sort){
            this.page=1;
            this.search.SortKey=sort;
            let params=this.$route.query;
            params.sort=sort;
            params.page=this.page;
            // 加载区域搜索信息
            this.loadTypeSearch(params);
        },
        // 后退方法
         back(){
            this.$router.push(this.search.backUrl);
        },
        loadData(){
            this.page++;
            let params=this.$route.query;
            params.page=this.page;
            this.loadTypeSearch(params);
        }
    },
    mounted() {
        // 加载搜索信息
        this.loadTypeSearch(this.$route.query);
        // 加载筛选标题信息
        this.loadFiliter();

    },
    beforeRouteUpdate (to, from, next) {
        this.loadTypeSearch(to.query);
        next();
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            if(from.path!="/detail"){
                vm.setbackUrl(from.path);
            }
        })
    }
   
};
</script>
<style lang="less">
.sr {
    height: 100%;
    padding-top: 100px;
    &-filter {
        display: flex;
        height: 80px;
        align-items: center;
        font-size: 28px; /*px*/
        color: #333;
        background-color: #fff;
        > li {
            width: 25%;
            text-align: center;
            border-right: 0.02rem solid #eee;
        }
        .active{
            color:#ef4562
        }
    }
    &-wrap {
        height: 100%;
        .error{
            font-size: 30px;/*px*/
            padding: 60px;
        }
        &-goodlist {
            display: flex;
            flex-wrap: wrap;
            .title-block {
                display: inline-block;
                height: 36px;
                color: #5a6f7a;
                background-color: #eff3f6;
                padding: 0 6px;
                margin: 5px 6px;
            }
        }
    }
}
</style>



