import jsonp from 'fetch-jsonp';
function paseParams(params){
    let str="?";
    for (const key in params) {
       str+=`${key}=${params[key]}&`;
    }
    str=str.replace(/&$/,"");
    return str;
}
export default{
    request(url,params={}){
        return jsonp(url+paseParams(params))
        .then((response)=>{
            return response.json();
        }).catch((e)=>{
            console.log(e);
        })
    }
}


